.blog_container {
  // background-color: red;
  height: 81vh;
  width: 60vw;
  position: relative;
  top: 15vh;
  display: block;
  .blog_item_container {
    // background-color: hotpink;
    height: 27vh;
    width: 60vw;
    display: flex;
    justify-content: space-evenly;
    .blog_image {
      width: 25vh;
      height: 25vh;
      // background-color: red;
      img {
        width: 100%;
        height: 100%;
        border-radius: 5px;
      }
    }
    .blog_sum {
      width: 45vw;
      height: 25vh;
      display: block;
      // background-color: red;
      .blog_sum_header {
        // margin-top: 1.7rem;
        padding: 1.5rem;
        font-size: 1.8rem;
        font-weight: 600;
        text-align: center;
        text-transform: uppercase;
      }
      .blog_sum_text {
        font-size: 1.1rem;
        text-align: center;
      }
      .blog_button {
        text-align: center;
        padding: 1rem;
        button {
          width: 7vw;
          height: 5vh;
          background-color: $yellow;
          border: none;
          border-radius: 10px;
          font-size: large;
        }
      }
    }
  }
}

//POST

.post_container {
  text-align: center;
  position: relative;
  // overflow-x: hidden;
  // overflow-y: hidden;
  display: block;
  // width: 80vw;
  top: 15vh;
  // background-color: yellowgreen;
  .post_content {
    padding: 1rem;
    text-align: left;
    font-size: 1.6rem;
    line-height: 2rem;
    font-weight: 350;
  }
  .post_header {
    // background-color: red;
    text-transform: uppercase;
  }
  .post_quote {
    padding: 2rem;
    font-size: 1.6rem;
    line-height: 2rem;
    font-weight: 450;
    text-decoration: none;
    color: $yellow;
  }
  .image_container {
    img {
      width: 50vw;
      max-width: 50vw;
    }
  }
}
