.about_me_container {
    // background-color: red;
    position: relative;
    top: 15vh;
    height: 84vh;
    display: flex;
    justify-content: space-between;
    .aboutme_image{
        padding: 15px;
        img{
            width:auto;
            height: 50vh;
        }
    }
    .aboutme_content{
        .aboutme_header{
            text-align: center;
            font-size: 2rem;
            font-weight: 600;
            margin-top: 1.7rem;
            padding-bottom: 1.4rem;
        }
        height:50vh;
        // background-color: green;
        width:auto;
        .aboutme_paragraph{
            padding: 10px;
            font-size: 1.5rem;
            text-align: justify;
            @include media("<=LGdesktop") {
                padding: 10px;
            font-size: 1.1rem;
            text-align: justify;
              }
        }
    }
}